// pages / dashboard / announcement / add-announcement

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class AddAnnouncement extends React.Component {
    constructor() {
        super();
        this.state = {
            announcementTypeOption: 'success',
            positionOption: 'toast-top-right'
        }
    }


    componentDidMount() {
        $('.summernote').summernote();

        $('.note-editable').focus();
    }

    testNotification() {
        toastr.options = {
          "closeButton": true,
          "debug": false,
          "progressBar": true,
          "preventDuplicates": false,
          "positionClass": this.state.positionOption,
          "onclick": null,
          "showDuration": "400",
          "hideDuration": "1000",
          "timeOut": "7000",
          "extendedTimeOut": "1000",
          "showEasing": "swing",
          "hideEasing": "linear",
          "showMethod": "fadeIn",
          "hideMethod": "fadeOut"
        };
        let msg = this.refs.announcementMsg.value || "Empty Message",
            title = this.refs.announcementTitle.value || "Empty Title";
        toastr[this.state.announcementTypeOption](msg, title);
    }

    clearNotification() {
        toastr.clear();
    }

    setAnnouncementTypeOption(e) {
        console.log(e.target.value);
        this.setState({announcementTypeOption: e.target.value});
    }

    setPositionOption(e) {
        this.setState({positionOption: e.target.value});
    }


    render() {
        if (this.props.user.loggedIn) {

            return (
              <span>

                    {/* Start of Helmet */}
                    <Helmet title="Announcement - add" meta={[
                        {
                            name: 'description',
                            content: 'Announcement - add'
                        }, {
                            name: 'keywords',
                            content: 'Account'
                        }
                    ]}/>
                    {/* End of Helmet */}



                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-8">
                            <h2>Announcement</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Edit Announcement</strong>
                                </li>
                            </ol>
                        </div>
                    </div>
                    {/* End of Heading */}




                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInDown">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Announcement Content</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                <i className="fa fa-chevron-up"></i>
                                            </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                <i className="fa fa-wrench"></i>
                                            </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                <i className="fa fa-times"></i>
                                            </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content no-padding">
                                        <div className="summernote">
                                            <br/><br/><br/><br/><br/><br/><br/>
                                            <br/><br/><br/><br/><br/><br/><br/>
                                        </div>
                                    </div>
                                </div>
                            </div>



                            <div className="col-lg-12">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Announcement Settings</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                <i className="fa fa-chevron-up"></i>
                                            </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                <i className="fa fa-wrench"></i>
                                            </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li>
                                                  <a href="#">Config option 1</a>
                                                </li>
                                                <li>
                                                  <a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                <i className="fa fa-times"></i>
                                            </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <div className="row">
                                            <div className="col-md-4">
                                                <div className="form-group">
                                                    <label htmlFor="title">Announcement Title</label>
                                                    <input id="title" type="text" className="form-control" placeholder="Title for Announcement" ref="announcementTitle" />
                                                </div>
                                                <div className="form-group">
                                                    <label htmlFor="message">Short Message</label>
                                                    <textarea className="form-control textarea-no-resize" id="message" rows="3" placeholder="Short Message for Announcement" ref="announcementMsg"></textarea>
                                                </div>
                                            </div>

                                            <div className="col-md-2">
                                                <div className="form-group" id="toastTypeGroup">
                                                    <label>Announcement Type</label>
                                                    <br/>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioSuccess" name="toasts" value="success" checked={this.state.announcementTypeOption === 'success'} onChange={this.setAnnouncementTypeOption.bind(this)} />
                                                        <label htmlFor="radioSuccess">
                                                          Success
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioInfo" name="toasts" value="info" checked={this.state.announcementTypeOption === 'info'} onChange={this.setAnnouncementTypeOption.bind(this)} />
                                                        <label htmlFor="radioInfo">
                                                            Info
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioWarning" name="toasts" value="warning" checked={this.state.announcementTypeOption === 'warning'} onChange={this.setAnnouncementTypeOption.bind(this)} />
                                                        <label htmlFor="radioWarning">
                                                            Warning
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioError" name="toasts" value="error" checked={this.state.announcementTypeOption === 'error'} onChange={this.setAnnouncementTypeOption.bind(this)} />
                                                        <label htmlFor="radioError">
                                                            Error
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className="col-md-6">
                                                <div className="form-group" id="positionGroup">
                                                    <label>Position</label>
                                                    <br/>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioTopRight" name="positions" value="toast-top-right" checked={this.state.positionOption === 'toast-top-right'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioTopRight">
                                                            Top Right
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioBottonRight" name="positions" value="toast-bottom-right" checked={this.state.positionOption === 'toast-bottom-right'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioBottonRight">
                                                            Bottom Right
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioBottomRight" name="positions" value="toast-bottom-left" checked={this.state.positionOption === 'toast-bottom-left'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioBottomRight">
                                                            Bottom Left
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioTopLeft" name="positions" value="toast-top-left" checked={this.state.positionOption === 'toast-top-left'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioTopLeft">
                                                            Top Left
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioTopFullWidth" name="positions" value="toast-top-full-width" checked={this.state.positionOption === 'toast-top-full-width'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioTopFullWidth">
                                                            Top Full Width
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioBottomFullWidth" name="positions" value="toast-bottom-full-width" checked={this.state.positionOption === 'toast-bottom-full-width'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioBottomFullWidth">
                                                            Bottom Full Width
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioTopCenter" name="positions" value="toast-top-center" checked={this.state.positionOption === 'toast-top-center'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioTopCenter">
                                                            Top Center
                                                        </label>
                                                    </div>
                                                    <div className="radio radio-position-inline">
                                                        <input type="radio" id="radioBottonCenter" name="positions" value="toast-bottom-center" checked={this.state.positionOption === 'toast-bottom-center'} onChange={this.setPositionOption.bind(this)} />
                                                        <label htmlFor="radioBottonCenter">
                                                            Bottom Center
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="space-25"></div>

                                        <div className="row pull-right">
                                            <div className="col-lg-12">
                                                <button type="button" className="btn btn-info btn-space" id="showtoast" onClick={this.testNotification.bind(this)}>Test Notification</button>
                                                <button type="button" className="btn btn-white btn-space" id="cleartoasts" onClick={this.clearNotification.bind(this)}>Clear Test Notification</button>
                                                <button type="button" className="btn btn-success btn-space" id="showtoast">Save without Push Notification</button>
                                                <button type="button" className="btn btn-primary btn-space" id="showtoast">Save with Push Notification</button>
                                            </div>
                                        </div>
                                        <div className="clearfix"></div>

                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                    {/* End of Content */}

                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(AddAnnouncement);



